import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
    state: {
        namePages:null,
    },
    mutations: {
        setNamePages(state,namePages){
            state.namePages = namePages
        },
    },
    getters: {
        namePages: state=> state.namePages,
      }
})


